.wrap {
  max-width: 1440px;
  margin: 0 auto;
  margin-top: 100px;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: flex-start;
  gap: 20px;

  > p {
    text-transform: uppercase;
    font-size: 1.5rem;
    font-weight: 600;
    color: #ffc85e;

    > span {
      color: #111111;
    }
  }

  &Items {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }
}
