.wrap {
  margin-top: 30px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  h1 {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    font-size: 40px;
    line-height: 120%;
    text-transform: uppercase;
  }
    
}

.items {
  display: flex;
  flex-direction: column;
  
}

